import React from 'react';
import formatCurrency from '../Currency';

function ProductModal(props) {
    const {product} = props;
    return (
        <div className='product-modal-div'>
            <img className='product-modal-img' src={product.Image}/>
            <div className='product-modal-right'>
                <h5 className='product-quantity'>{product.Title}</h5>
                <span className='product-price'>{formatCurrency(product.Price)}</span>
                <div style={{display:'flex', gap: "1rem", alignItems: 'center'}}>
                    <span className='product-quantity'>Available Sizes</span>
                    <div style={{display: 'flex', gap: '1rem'}}>
                        {product.AvailableSize.map((size)=>(
                            <div className='available-sizes'>{size}</div>
                        ))}
                    </div>
                </div>
                <p className='product-Description'>{product.Description}</p>


            </div>
            
        </div>
    )
}

export default ProductModal;
